const Styles = {
    body: {
        background: '#ECEFF1',
        color: "rgba(0, 0, 0, 0.87)",
        fontFamily: "Roboto, Helvetica, Arial, sans-serif",
        margin: 0,
        padding: 0,
    },
    message: {
        background: "white",
        maxWidth: "360px",
        margin: "100px auto 16px",
        padding: "32px 24px",
        borderRadius: "3px",
        boxSshadow: "0 1px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24)",
    },
    messageH2: {
        color: "#ffa100",
        fontWeight: "bold",
        fontSize: "16px",
        margin: "0 0 8px",
    },
    messageH1: {
        fontSize: "22px",
        fontWeight: "300",
        color: "rgba(0, 0, 0, 0.6)",
        margin: "0 0 16px",
    },

    messageP: {
        lineHeight: "140%",
        margin: "16px 0 24px",
        fontSize: "14px",
    },
    messageA: {
        display: "block",
        textAlign: "center",
        background: "#039be5",
        textTransform: "uppercase",
        textDecoration: "none",
        color: "white",
        padding: "16px",
        borderRadius: "4px",
        boxSshadow: "0 1px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24)",
    },
    load: {
        color: "rgba(0, 0, 0, 0.4)",
        textAlign: "center",
        fontSize: "13px",
    }
}

export default Styles


